"use client"

import { useState } from 'react'
import { Card } from "@/components/ui/card"
import { Input } from "@/components/ui/input"
import { Button } from "@/components/ui/button"
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table"

interface Student {
  id: number
  name: string
  age: number
  grade: string
}

export default function Home() {
  const [isLoggedIn, setIsLoggedIn] = useState(false)
  const [username, setUsername] = useState('')
  const [password, setPassword] = useState('')
  const [searchTerm, setSearchTerm] = useState('')
  const [students, setStudents] = useState<Student[]>([
    { id: 1, name: "John Doe", age: 16, grade: "10th" }
  ])

  const handleLogin = () => {
    if (username === 'admin' && password === 'admin123') {
      setIsLoggedIn(true)
    } else {
      alert('Invalid credentials')
    }
  }

  const handleLogout = () => {
    setIsLoggedIn(false)
    setUsername('')
    setPassword('')
  }

  const filteredStudents = students.filter(student => 
    student.name.toLowerCase().includes(searchTerm.toLowerCase()) ||
    student.grade.toLowerCase().includes(searchTerm.toLowerCase()) ||
    student.age.toString().includes(searchTerm)
  )

  if (!isLoggedIn) {
    return (
      <div className="min-h-screen bg-gray-100 flex items-center justify-center">
        <Card className="w-[400px] p-6">
          <h2 className="text-2xl font-bold text-center mb-6">Login</h2>
          <div className="space-y-4">
            <div>
              <label className="block text-sm font-medium mb-1">Username</label>
              <Input
                type="text"
                value={username}
                onChange={(e) => setUsername(e.target.value)}
                placeholder="Enter username"
              />
            </div>
            <div>
              <label className="block text-sm font-medium mb-1">Password</label>
              <Input
                type="password"
                value={password}
                onChange={(e) => setPassword(e.target.value)}
                placeholder="Enter password"
              />
            </div>
            <Button 
              className="w-full bg-[#1a237e] hover:bg-[#1a237e]/90"
              onClick={handleLogin}
            >
              Login
            </Button>
          </div>
        </Card>
      </div>
    )
  }

  return (
    <div className="min-h-screen bg-gray-100">
      <div className="flex">
        {/* Sidebar */}
        <div className="w-64 min-h-screen bg-[#1a237e] p-4">
          <div className="space-y-2">
            <Button variant="ghost" className="w-full text-white justify-start">
              Dashboard
            </Button>
            <Button variant="ghost" className="w-full text-white justify-start">
              Students
            </Button>
            <Button variant="ghost" className="w-full text-white justify-start">
              Reports
            </Button>
            <Button variant="ghost" className="w-full text-white justify-start">
              Settings
            </Button>
          </div>
          <Button 
            className="w-full mt-auto bg-[#009688] hover:bg-[#009688]/90 absolute bottom-4 left-0 mx-4"
            onClick={handleLogout}
          >
            Logout
          </Button>
        </div>

        {/* Main Content */}
        <div className="flex-1 p-8">
          <div className="flex justify-between items-center mb-6">
            <h1 className="text-2xl font-bold">Student Management</h1>
            <div className="flex gap-2">
              <Input
                placeholder="Search students..."
                value={searchTerm}
                onChange={(e) => setSearchTerm(e.target.value)}
                className="w-[300px]"
              />
              <Button className="bg-[#009688] hover:bg-[#009688]/90">
                Search
              </Button>
            </div>
          </div>

          <Card className="mb-6">
            <Table>
              <TableHeader>
                <TableRow>
                  <TableHead>ID</TableHead>
                  <TableHead>Name</TableHead>
                  <TableHead>Age</TableHead>
                  <TableHead>Grade</TableHead>
                </TableRow>
              </TableHeader>
              <TableBody>
                {filteredStudents.map((student) => (
                  <TableRow key={student.id}>
                    <TableCell>{student.id}</TableCell>
                    <TableCell>{student.name}</TableCell>
                    <TableCell>{student.age}</TableCell>
                    <TableCell>{student.grade}</TableCell>
                  </TableRow>
                ))}
              </TableBody>
            </Table>
          </Card>

          <div className="flex gap-2">
            <Button className="bg-[#009688] hover:bg-[#009688]/90">
              Add Student
            </Button>
            <Button className="bg-[#009688] hover:bg-[#009688]/90">
              Edit Student
            </Button>
            <Button className="bg-[#009688] hover:bg-[#009688]/90">
              Delete Student
            </Button>
          </div>
        </div>
      </div>
    </div>
  )
}
